<template>
  <div class="login">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span>登录</span>
      </template>

      <template #content>
        <el-form
          :model="ruleForm"
          hide-required-asterisk
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="用户名" prop="username" label-width="100px">
            <el-input
              v-model="ruleForm.username"
              placeholder="请输入用户名"
              type="text"
              autocomplete="off"
              style="width:260px"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password" label-width="100px">
            <el-input
              v-model="ruleForm.password"
              placeholder="请输入密码"
              type="password"
              autocomplete="off"
              style="width:260px"
            ></el-input>
          </el-form-item>
          <el-form-item >
            <el-button  @click="login" type="primary">登录</el-button>
          </el-form-item>
        </el-form>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Panel from "../containers/Panel.vue";
export default {
  components: {
    Panel
  },
  data() {
    return {
      ruleForm: {
        username: "admin",
        password: "47495417-7b42-4aa0-b3ad-e170e5a7d92c"
      },
      rules: {
        username: [
          {
            required: true,
            trigger: "blur",
            message: "用户名不能为空"
          }
        ],
        password: [
          {
            required: true,
            trigger: "blur",
            message: "密码不能为空"
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions(["getInfo"]),
    login() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          // 6d25e839-bb57-4786-8cfc-8a7fd7ac7982
          // console.log(this.ruleForm.password);
          this.getInfo(this.ruleForm.password)
            .then(() => {
              this.$router.push("/");
            })
            .catch(() => {
              console.log("error submit!!");
            });
        }
      });
    }
  }
};
</script>

<style lang='less'>
.login{
  .el-form{
    width: 400px;
    margin: 0 auto;
    .el-form-item{
      text-align: center;
      .el-button{
        width: 100px;
      }
    }
  }
}
</style>